.layout-container{width:100%;height:100%;color:var(--fontColorMain);background:var(--bgXl);font-family:var(--fontFamily);display:flex;flex-direction:column}.layout-overlay{position:absolute;top:0;left:0;width:100%;height:100%;background-color:var(--bgColorMain);opacity:var(--bgOpacity)}.site-header{border-bottom:2px solid var(--borderColorMain);position:relative}.page-content-wrapper{width:100%;height:100%;position:relative}.page-content{width:100%;height:100%;padding:10px 20px;position:absolute;z-index:0;top:0;left:0;overflow-y:scroll;overflow-x:scroll;scrollbar-width:none;-ms-overflow-style:none}.page-content::-webkit-scrollbar{width:0px;background:transparent}@media screen and (max-width: 640px){.layout-container{background:var(--bgSm)}}@media screen and (min-width: 641px)and (max-width: 1280px){.layout-container{background:var(--bgMed)}}@media screen and (min-width: 1281px)and (max-width: 1920px){.layout-container{background:var(--bgLarge)}}