.upcoming-features-container p{font-size:95%}.upcoming-features-container h5{font-size:90%}.upcoming-features-container li li{list-style-type:disc}.welcome-container-wrapper{width:100%;height:100%;position:relative}.welcome-container{width:100%;height:100%;position:absolute;z-index:0;top:0;left:0;padding:5px 15px 15px 15px;overflow-y:scroll;overflow-x:scroll;scrollbar-width:none;-ms-overflow-style:none}.welcome-container::-webkit-scrollbar{width:0px;background:transparent}.welcome-container h3{text-align:center;margin-bottom:13px}.welcome-container ul{padding-left:25px}.welcome-container h4{text-decoration:underline;font-style:italic;margin-bottom:5px;color:var(--fontColorAlt)}.welcome-container h4:not(:first-of-type){margin-top:15px}.welcome-container button{margin-top:10px;float:right}.close-modal-button{position:relative;right:10px;display:grid;justify-content:right}@media screen and (max-width: 640px){.welcome-container{padding:0}.welcome-container h3{font-size:120%}.welcome-container li{font-size:.95em}.welcome-container h4{margin-top:0}}