.container{width:100%;display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;padding-bottom:30px}.page-options{width:80%;display:flex;justify-content:center}.page-option{margin:0px 5px}div.search-bar{width:100%;margin-top:5px;padding:0px;display:flex;align-items:center;background-color:var(--bgColorAlt);color:var(--fontColorMain)}span.search-bar{margin:0 10px 0 5px;width:max-content}@media screen and (max-width: 640px){.container{font-size:.8em}span.search-bar{display:none}}