.modal-container{position:fixed;z-index:10;top:0px;left:0px;width:100%;height:100%;background-color:rgba(0,0,0,.5);color:var(--fontColorMain);display:flex;justify-content:center;align-items:center}.modal-content{width:60%;height:60%;background-color:var(--bgColorMain);border:1px solid var(--borderColorAlt);border-radius:10px;padding:10px 10px 20px 10px}@media screen and (max-width: 640px){.modal-content{width:90%;height:90%;padding:15px}}