.collapsible-section{width:100%;margin:20px 0 0 0;padding:10px 45px;border:3px solid var(--borderColorMain);color:var(--fontColorMain);background-color:var(--bgColorAlt);opacity:.8;border-radius:30px}.trigger{display:grid;justify-content:center;font-size:1.5em;padding:7.5px 0 5px 0;cursor:pointer}.trigger.open{margin-bottom:15px}.collapsible-content{margin-bottom:20px}@media screen and (max-width: 640px){.collapsible-section{padding:10px 15px}.collapsible-content{margin-bottom:10px}}@media screen and (min-width: 641px)and (max-width: 1280px){.collapsible-section{padding:10px 20px}}